.header-sub {
  width: 100%;
  background-color: $color-bg-index;

  &::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: $color-bg-title;
    display: block;
  }

  &--gray {
    background-color: $color-bg-title;
  }
}

.header-sub__container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  background-color: $color-bg-index;

  &--gray {
    background-color: $color-bg-title;
  }

  @include viewport--md {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 11px;
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 5px;
  }

  @include viewport--lg {
    padding-top: 8px;
  }
}

.header-sub__list {
  @include list-reset;
  background-color: $color-white;
  flex-direction: column;

  &--closed {
    display: none;
  }

  @include viewport--md {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 700px;
    background-color: transparent;
    margin: 0;
  }
}

/*
.header--nojs {
  display: block;
  z-index: 0;
}
*/

.header--opened .header-sub__list--opened {
  display: block;
  width: 100%;
  position: absolute;
  z-index: 100;
}

.header-sub__item {
  width: 100%;
  height: 79px;
  border-bottom: 1px solid $color-bg-title;
  display: flex;
  align-items: center;
  padding-left: 70px;

  @include viewport--md {
    border: none;
    padding: 0;
    height: auto;
    width: auto;
  }
}

.header-sub__item:not(:last-child) {
  @include viewport--md {
    margin-right: 24px;
  }

  @include viewport--lg {
    margin-right: 50px;
  }
}

.header-sub__link {
  text-decoration: none;
  color: $color-title;
  font-weight: 700;
  font-size: 17px;
  line-height: 1.41;
  padding: 10px 6px;
  position: relative;

  &:hover {
    color: rgba(0, 0, 0, 0.5);
  }

  &:active {
    color: rgba(0, 0, 0, 0.3);
  }

  &:focus-visible {
    outline: 2px solid $color-focus;
    border-radius: 4%;
  }

  @include viewport--md {
    font-weight: 700;
    font-size: 15px;
    line-height: 1.6;
  }
}

.header-sub__icon {
  position: absolute;
  left: -37px;
  top: 14px;
}

.header-sub__link:hover .header-sub__icon {
  color: $color-green;
  opacity: 1;
}

.header-sub__link:active .header-sub__icon {
  color: $color-green;
  opacity: 0.3;
}

.header-sub__delivery {
  display: none;
  font-size: 15px;
  line-height: 1.6;

  @include viewport--md {
    display: block;
    margin-right: 5px;
  }
}

.item-hidden-mobile {
  display: none;
}

.item-hidden-tablet {
  @include viewport--md {
    display: none;
  }

  @include viewport--lg {
    display: flex;
  }
}

.item-hidden-desktop {
  @include viewport--lg {
    display: none;
  }
}
