html {
  height: 100%;
  box-sizing: border-box;
}

*,
::before,
::after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-size: 13px;
  font-weight: 400;
  line-height: 2;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
}

.body__index {
  background-color: #fafafa;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
.container {
  width: 100%;
  max-width: 767px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}
@media (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 1149px;
