.header__toggle {
  position: absolute;
  right: 31px;
  top: 23px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
  padding: 0;
}
.header__toggle--opened {
  right: 31px;
  top: 27px;
  width: 22px;
  height: 22px;
}
.header__toggle--closed {
  right: 29px;
  top: 22px;
  width: 22px;
  height: 22px;
}
.header__toggle--closed::before {
  content: "";
  display: block;
  top: 0;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 #000000, 0 12px 0 #000000;
}
.header__toggle--closed--nojs {
  display: none;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__toggle--opened::before,
.header__toggle--opened::after {
  position: absolute;
  content: "";
  height: 2px;
  width: 25px;
  background-color: #000000;
  display: block;
}

.header__toggle--opened::before {
  transform: rotate(45deg);
}

.header__toggle--opened::after {
  transform: rotate(-45deg);
}

